.background {
  padding: 92px 40px 132px 40px;
  background-color: #f0f2f5;
}
.body {
  display: flex;
  justify-content: center;
  width: 100%;
}
@media (max-width: 900px) {
  .background {
    padding: 20px 0px 80px 0px;
  }
  .body {
    flex-direction: column;
    align-items: center;
  }
}
